import { NgModule } from '@angular/core';
import { Route, RouterModule } from '@angular/router';

import { DetailCardViewComponent } from './detail-card-view.component';

const routes: Route[] = [
    {
        path: '',
        component: DetailCardViewComponent
    },
    { path: '**', redirectTo: '' }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DetailCardViewRouting {}
