    private subscription = null;

    constructor(private renderer: Renderer2, private demoService: DemoService) {
        this.subscription = this.demoService.getMenuEvents().subscribe((event) => {
            if (event === 'transitionend' && this.detailCard.visible) {
                this.detailCard.hide();
                this.detailCard.show();
                this.detailCard.placeComponent();
            }
        });
    }

    public ngOnDestroy() {
        this.subscription.unsubscribe();
    }
