import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class DemoService {
    private menuAction = new Subject<string>();

    public menuEvent(event) {
        this.menuAction.next(event);
    }

    public getMenuEvents(): Observable<string> {
        return this.menuAction.asObservable();
    }
}
